import React from 'react';

import Api from 'api';
// import classNames from 'classnames';
import styles from "./reservation-success.scss";
import * as Tool from 'tool';
import * as getUserIdInfo from 'tool/getUserId';
import { inject, observer } from 'mobx-react';

import classNames from "classnames";
import * as Common from "../../tool/common";

@inject('Toast', 'ComNoContent', 'Header')
@observer
export default class ReservationSuccess extends React.Component {
  constructor (props){
    super(props);
    this.state = {
    };
  }

  componentDidMount() {
    // 埋点
    let { userId } = getUserIdInfo.getUserId();
    Common.trackCustomPageInit({
      'pageId': 'tmfy_book_result_pv',
      'site_id': 1211120,
      'category_id': 1595,
      'sub_category_id': 18468
    }, {
      'userId': userId,
      'type': parseInt(Tool.getUrlParam('type')) === 1 ? 0 : 1
    });
    this.props.Header.$setTitle('预约成功');
    Tool.setTitle('预约成功');
  }

  // 返回
  handleGoBack = () => {
    // 埋点
    this.handleTrackCustomEvent('tmfy_book_result_back_click');

    // 参数需要区分维修和保养
    if (parseInt(Tool.getUrlParam('type')) === 2) { // 维修
      Common.pageJumpHandel('dealerList', {type: 1});
    } else { // 保养
      Common.pageJumpHandel('serviceList');
    }
  }

  // 查看预约订单，跳转我的预约单
  handleLinkToMyOrder = () => {
    // 埋点
    this.handleTrackCustomEvent('tmfy_book_result_check_click', Tool.getUrlParam('orderId'));

    Common.pageJumpHandel('reservationsList', {});
  }

  // 埋点事件
  handleTrackCustomEvent(action, objectId = '') {
    let { userId } = getUserIdInfo.getUserId();
    // eslint-disable-next-line
    trackCustomEvent('yc_page_common_click', {
      'bus': '1',
      'refpage': Tool.getCookie('refpage'),
      'channel': Tool.getCookie('channel'),
      'city': Tool.getCookie('cityId') || 110100,
      'action': action
    }, {
      'userId': userId,
      'objectId': objectId,
      'type': parseInt(Tool.getUrlParam('type')) === 1 ? 0 : 1
    });
  }

  render() {
    return (
      <div className={styles.reservation_success_container}>
        <img className={ styles.icon_right_big } src={ require('../../assets/images/icon-right-big.png') }/>
        <div className={ styles.title }>恭喜您，预约成功</div>
        <div className={ styles.tip }>请您保持手机畅通将于一个工作日之内与您联系</div>
        <div className={ styles.btn_box }>
          <div className={ classNames(styles.btn_item, styles.order) } onClick={ this.handleLinkToMyOrder }>查看预约订单</div>
          <div className={ classNames(styles.btn_item, styles.back) } onClick={ this.handleGoBack }>返回</div>
        </div>
      </div>
    );
  }
}